import React, { Fragment } from "react";
import { Input, Badge, Popup, Card } from '@scuf/common';

export default class InputNumber extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fileError: "",
            value: 0,
            acceptedFiles: [],
            rejectedFiles: []
        };
    }

    handleOnChange = (dato) => {
        if (!isNaN(dato.val)) {
            this.setState({ value: dato.val });
            this.props.onDato(dato);
        }
        else {
            this.props.onRemoveDato(dato);
        }

        if (dato.val === "") {
            this.props.onRemoveDato(dato);
        }
    }

    handleValue(a, b) {
        return a === b
    }


    componentWillMount() {
        var nvalue = ""
        this.props.items.map((item) =>
            nvalue = item.value
        )
        this.setState({ value: nvalue });
    }

    async componentDidMount() {

        if (this.state.value !== "") {
            var dato = await { key: this.props.idGrupo, id: 1, val: this.state.value }
            this.handleOnChange(dato)
        }

    }

    render() {

        var idGrupo = this.props.idGrupo
        return (

            <React.Fragment>

                {this.props.items.map((item) => (
                    <React.Fragment>
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <Badge color={this.state.value === "" ? "red" : "green"} empty={true}>&nbsp;</Badge>

                        <Popup className="popup-theme-wrap" element={item.label} position="right center" on="hover">
                            <Card>
                                <Card.Content>
                                    {
                                        item.label == "Parámetro: Min Sample" ?
                                            <Fragment>
                                                Número entero que indica cuantas iteraciones se realizarán sobre cada valor de min_cluster_size para elección del par de hiperparámetros [min_cluster_size, min_samples]
                                                <br />
                                                <a href="https://hdbscan.readthedocs.io/en/latest/parameter_selection.html#selecting-min-cluster-size" target="_blank">Mas Información</a>
                                            </Fragment>

                                            :
                                            item.label == "Período de agrupación de datos (minutos)" ?
                                                <Fragment>
                                                    Periódo de muestreo del dataset usado (minutos)
                                                </Fragment>

                                                :
                                                item.label == "Puerto de disponibilización" ?
                                                    <Fragment>
                                                        Puerto sobre el cual se levantará el modelo de xgboost
                                                    </Fragment>

                                                    : "X"

                                    }
                                </Card.Content>
                            </Card>
                        </Popup>

                        <Input
                            placeholder={"Valor Numérico"}
                            type={"number"}
                            key={item.id}
                            fluid={true}
                            value={this.state.value}
                            onChange={(value) => this.handleOnChange({ key: idGrupo, id: item.id, val: value })}
                            min={0}
                        />
                    </React.Fragment>
                ))}


                {/*<React.Fragment>*/}

                {/*    <LocalizationConfig languageCode={this.state.lng}>*/}
                {/*        <FileDrop*/}
                {/*            label="Fotos del Vehiculo"*/}
                {/*            buttonText="Subir Foto"*/}
                {/*            resetButtonText="Eliminar Fotos"*/}
                {/*            acceptedTypes={['image/*']}*/}
                {/*            placeholder="Arrastrar Foto (maximo: 5MB)"*/}
                {/*            maxSize="1000"*/}
                {/*            error={this.state.fileError}*/}
                {/*            onAccepted={(files) => this.setState({ acceptedFiles: [...acceptedFiles, ...files] })}*/}
                {/*            onRejected={(files) => this.setState({ rejectedFiles: [...rejectedFiles, ...files], fileError: "uno o mas archivos fueron rechazados" })}*/}
                {/*            onReset={() => this.setState({ rejectedFiles: [], acceptedFiles: [], fileError: "" })}*/}
                {/*        />*/}
                {/*        <h5>Fotos Aceptadas:</h5>*/}
                {/*        {acceptedFiles && acceptedFiles.map(file => <p>{file.name} - {file.size} bytes</p>)}*/}
                {/*        <h4>Fotos Rechazadass:</h4>*/}
                {/*        {rejectedFiles && rejectedFiles.map(file => <p>{file.name} - {file.size} bytes</p>)}*/}
                {/*    </LocalizationConfig>*/}
                {/*</React.Fragment>*/}

            </React.Fragment>
        );
    }
}
