import { useState, useEffect } from 'react'

import { Modal, InputLabel, Notification } from '@scuf/common'
import { DataTable } from '@scuf/datatable';
import { Icon, Button, Loader, Card, Header } from '@scuf/common';

import { useDispatch } from 'react-redux'
import DataTabDataAPI from './DatTabDataAPI'
import baseServices from '../../../services/base.services';
import '@scuf/datatable/honeywell-compact-dark/theme.css'
import { Fragment } from 'react';
import 'react-toastify/dist/ReactToastify.css';
import '../../../../src/toast-notification-wrap.css'

import Formulario from '../../../components/formDS/Formulario'
import SubirArchivo from '../../../components/fileupload/miFileUpload'
const ReactToastify = require('react-toastify');

const DatTab = ({ url, body, title }) => {

    const dispatch = useDispatch();
    const [state, setState] = useState([])
    const [modal, setModal] = useState(false);
    const [add, setAdd] = useState(false);
    const [addCsv, setAddCsv] = useState(false);
    const [id, setId] = useState(0);
    const [selectedRow, setSelectedRow] = useState([])
    const [deleteRows, setDeleteRows] = useState(false)
    const [isLoading, setIsLoading] = useState(false)

    useEffect(() => {
        const getData = async () => {
            try {
                if (body) {
                    var response = await baseServices.getData(body, url);
                    if (response.data && response.data.length > 0) {
                        setState(DataTabDataAPI(response))
                    }
                } else { }

            } catch (err) { }

        }

        getData();

    }, []);




    const delData = async () => {
        setIsLoading(true);
        try {
            if (body) {
                let bodydel = '?id=' + selectedRow[0].id;
                var responseDel = await baseServices.delData(bodydel, url);
                window.location.reload();
            } else { }

        } catch (err) { }

    }


    let handleDelete = () => {
        var res;
        //console.log(selectedRow[0].id)
        selectedRow.length < 1
            ?
            res = ReactToastify.toast(<ToastNotification Tipo="Delete" Texto={"Seleccione un DataSource"} closeToast={true} Severity="critical" />)
            :
            res = ReactToastify.toast(<ToastNotification Tipo="Delete" Texto={selectedRow.length == 1 ? "¿Eliminar " + selectedRow[0].conn_id + "?" : "¿Eliminar estos " + selectedRow[0].conn_id + " DataSources?"} closeToast={true} Severity="information" />)

        return res
    }

    let handleAdd = () => {
        var res;
        setAdd(true);
        return res
    }

    let handleAddCsv = () => {
        var res;
        setAddCsv(true);
        return res
    }



    const ToastNotification = ({ Tipo, closeToast, Texto, Severity }) => (
        Tipo == "Delete"
            ?
            Severity == "critical"
                ?
                <Notification
                    className="toast-notification"
                    severity={Severity}
                    onCloseClick={closeToast}
                    hasIcon={false}
                    title={Texto}
                    detailsText={<Button key={1} size="small" content="OK" onClick={closeToast} />}
                    onDetailsClick={closeToast}
                />
                :
                <Notification
                    className="toast-notification"
                    severity={Severity}
                    onCloseClick={closeToast}
                    hasIcon={false}
                    title={Texto}
                    detailsText={<Button key={1} size="small" content="Si" onClick={closeToast} />}
                    //detailsText="Eliminar"
                    onDetailsClick={() => { delData() }}
                />
            :
            <Fragment />

    )




    const Item = DataTable.HeaderBar.Item;
    return (
        <Fragment>
            {
                isLoading ?
                    <Fragment>
                        <Header menu={false} logo={false} title={'Eliminando Data Source...'}>
                        </Header>
                        <Card>
                            <Card.Header />
                            <Card.Content>
                                <Loader minHeight={400}>
                                    <div className="placeholder" />
                                </Loader>
                            </Card.Content>
                        </Card>
                    </Fragment>
                    :
                    add ?
                        <Formulario />
                        :
                        addCsv ? <SubirArchivo />
                            :
                            state.datos ?
                                modal ?
                                    <Modal closeIcon={true} onClose={() => setModal(false)} open={modal} closeOnDimmerClick={false} closeOnDocumentClick={false} >
                                        <Modal.Header>
                                            {state.datos[id].conn_id}
                                        </Modal.Header>
                                        <Modal.Content>
                                            <Fragment>
                                                <DataTable data={[state.datos[id]]} rows={10} search={true}
                                                    scrollable={true} scrollHeight="600%" reorderableColumns={true} resizableColumns={true}
                                                >
                                                    {
                                                        state.fields && state.fields.map((y, i) => (
                                                            <DataTable.Column key={i} field={y.field} header={y.field} sortable={true} editable={true} editFieldType="text" />
                                                        ))
                                                    }
                                                    <DataTable.Pagination />

                                                </DataTable>
                                            </Fragment>
                                        </Modal.Content>
                                        <Modal.Footer>

                                        </Modal.Footer>
                                    </Modal>
                                    :
                                    <Fragment>
                                        <InputLabel label={title} />
                                        <DataTable data={state.datos} rows={10} search={true} selection={selectedRow} onSelectionChange={e => setSelectedRow(e)} selectionMode="single"
                                            scrollable={true} scrollHeight="600%" reorderableColumns={true} resizableColumns={true}
                                            onCellClick={(data) => { setModal(true); setId(data.rowIndex) }}
                                        >
                                            <DataTable.HeaderBar>
                                                <Item
                                                    content="SQL"
                                                    icon={<Icon name="slidercontrols-plus" size="small" root="common" />}
                                                    onClick={() => handleAdd()}
                                                />

                                                <Item
                                                    content="CSV"
                                                    icon={<Icon name="slidercontrols-plus" size="small" root="common" />}
                                                    onClick={() => handleAddCsv()}
                                                />

                                                <Item
                                                    content="Eliminar"
                                                    icon={<Icon name="delete" size="small" root="common" />}
                                                    onClick={() => handleDelete()}
                                                />
                                            </DataTable.HeaderBar>

                                            <DataTable.Column key={1} field="conn_id" header="Conexión" sortable={true} />
                                            <DataTable.Column key={2} field="description" header="Descripción" sortable={true} />
                                            <DataTable.Column key={3} field="db_type" header="Base de Datos" sortable={true} />
                                            <DataTable.Column key={3} field="table_type" header="Tipo" sortable={true} />

                                            <DataTable.Pagination />

                                        </DataTable>

                                        <ReactToastify.ToastContainer
                                            hideProgressBar={false}
                                            closeOnClick={false}
                                            closeButton={false}
                                            newestOnTop={true}
                                            position="top-center"
                                            autoClose={5000}
                                            toastClassName="toast-notification-wrap"
                                        />

                                    </Fragment>


                                : <Fragment>
                                    <InputLabel label={title} />
                                    <DataTable data={state.datos} >
                                        <DataTable.HeaderBar>
                                            <Item
                                                content="Crear"
                                                icon={<Icon name="slidercontrols-plus" size="small" root="common" />}
                                                onClick={() => handleAdd()}
                                            />
                                            <Item
                                                content="Eliminar"
                                                icon={<Icon name="delete" size="small" root="common" />}
                                            />
                                        </DataTable.HeaderBar>
                                        <DataTable.Column key={1} field="conn_id" header="Conexión" />
                                        <DataTable.Column key={2} field="description" header="Descripción" />
                                        <DataTable.Column key={3} field="db_type" header="Base de Datos" />
                                        <DataTable.Column key={3} field="table_type" header="Tipo" />
                                    </DataTable>

                                </Fragment>
            }

        </Fragment>


    )
}

export default DatTab
