import React, { useState, useEffect } from "react";
import { Loader, Select, InputLabel } from "@scuf/common";
import Plot from 'react-plotly.js';
import baseServices from '../../services/base.services';
import { getJSDocDeprecatedTag } from "typescript";

const stateOptions = [{ value: 'AL', text: 'Alabama' }, { value: 'GA', text: 'Georgia' }, { value: 'HI', text: 'Hawaii' }];

const url = 'modos';
const body = '?id=pplot';
const bodyModels = '?id=models'


const PPlot = () => {

    const [state, setState] = useState({ dimensions: '', hidden: "hidden", showplot: false })
    const [models, setModels] = useState({ models: [] });
    const [seleccionado, setSeleccionado] = useState({ texto: "-" })

    useEffect(() => {
        const getModels = async () => {
            try {
                var response = await baseServices.getData(bodyModels, url);
                if (response.data && response.data.length > 0) {
                    setModels({ models: response.data, texto: "-" })
                }

            } catch (err) {
                setTimeout(() => {
                    getModels()
                }, 15000);

            }

        }
        getModels();


    }, []);


    function getDatos(modelo) {
        const getData = async () => {
            setState({ dimensions: '', hidden: "", width: window.innerWidth })
            const bodypplot = body + ";" + modelo

            try {
                if (bodypplot) {
                    var response = await baseServices.getData(bodypplot, url);
                    if (response.data && response.data.length > 0) {
                        setState({ dimensions: response.data, hidden: "hidden", width: window.innerWidth, showplot: true })
                        setSeleccionado({ texto: bodypplot })
                    }
                } else {
                }

            } catch (err) {
                setTimeout(() => {
                    getData()
                }, 15000);

            }

        }
        return getData();
    }



    var datos = {
        type: 'parcoords',
        labelfont: { color: '#e0e0e0', size: 14 },
        rangefont: { color: '#202020', size: 12 },
        tickfont: { color: '#e0e0e0', size: 12 },
        dimensions: state.dimensions && state.dimensions[0].array_to_json
        // [
        //     {
        //         label: 'Tag 1',
        //         values: [10, 40, 30]
        //     }, {
        //         label: 'Tag 2',
        //         values: [3, 1.5, 4]
        //     }, {
        //         label: 'Tag 3',
        //         values: [2, 4, 4]
        //     }, {
        //         label: 'Tag 4',
        //         values: [4, 2, 3]
        //     }
        // ]
    };

    var layout = {

        width: state.width * 0.85,
        height: 700,
        title: {
            text: 'Parallel Plot',
            font: {
                //family: 'Courier New, monospace',
                size: 20,
                color: '#e0e0e0'
            },
            xref: 'paper',
            x: 0.5,
            y: 0.99
        },
        plot_bgcolor: '#202020',
        paper_bgcolor: '#202020',



    }


    return (
        <React.Fragment>
            <div className={state.hidden}>
                <Loader text="Abriendo..." minHeight={400}>
                </Loader>
            </div>
            {/* <InputLabel label={JSON.stringify(seleccionado.texto)} /> */}

            <Select placeholder="Seleccione un Modelo" options={models.models} onChange={(value) => getDatos(value)} />
            {state.showplot &&
                <Plot
                    data={[datos]}
                    layout={layout}
                />
            }
        </React.Fragment>
    );

}


export default PPlot;
