import React, { useState, useEffect } from "react";
import baseServices from '../../services/base.services';
import { Grid, Select } from '@scuf/common'
import Line2 from '../../components/charts/Line/Line2'
import { Fragment } from "react";
//import DatTab from '../../components/common/DatTab/DatTab'
//import KPIStat from '../../components/common/KPIStat/KPIStat'
//import TitlePage from '../../components/TitlePage/TitlePage'


const url = 'modos';
const body0 = '?id=moperacion';
const bodyModels = '?id=models'

const Procesos = () => {

    const [models, setModels] = useState({ models: [] });
    const [body, setBody] = useState();

    useEffect(() => {
        const getModels = async () => {
            try {
                var response = await baseServices.getData(bodyModels, url);
                if (response.data && response.data.length > 0) {
                    setModels({ models: response.data, texto: "-" })
                }

            } catch (err) {
                setTimeout(() => {
                    getModels()
                }, 15000);
            }

        }
        getModels();

    }, []);


    function getDatos(modelo) {
        const bodymop = body0 + ";" + modelo
        setBody({ body: bodymop })
        return bodymop;
    }

    return (
        <React.Fragment>
            <Select placeholder="Seleccione un Modelo" options={models.models} onChange={(value) => getDatos(value)} />
            {
                body ?
                    <Grid className='m-0 p-0' >
                        <Grid.Row>

                        </Grid.Row>

                        <Grid.Row >
                            <Grid.Column xsWidth='12' sWidth='12' mWidth='12' lWidth='12' xlWidth='12'>
                                <div style={{ padding: '0px' }}>
                                    <Line2
                                        url={url}
                                        body={body.body}
                                        yAxisTitle=''
                                        titleCard='Modos de Operacion'
                                    />

                                    {/* <DatTab
                                url='modos'
                                body='?id=*'
                                title='Modelos'
                            /> */}

                                </div>

                            </Grid.Column>

                        </Grid.Row>

                    </Grid>
                    :
                    <></>
            }


        </React.Fragment>
    )
}

export default Procesos
