import React from "react";
import { Input, Badge } from '@scuf/common';

export default class TextoArea extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fileError: "",
            value: "",
            acceptedFiles: [],
            rejectedFiles: []
        };
    }

    handleOnChange = (dato) => {
        const lastchr = dato.val.slice(-1);
        var valor = "";
        if (lastchr == '' || lastchr == '0' || lastchr == '1' || lastchr == '2' || lastchr == '3' || lastchr == '4' || lastchr == '5' || lastchr == '5' || lastchr == '6' || lastchr == '7' || lastchr == '8' || lastchr == '9' || lastchr == 'h' || lastchr == 'd' || lastchr == 'w' || lastchr == 'm' || lastchr == 'y') {
            if (dato.val == 'h' || dato.val == 'd' || dato.val == 'w' || dato.val == 'm' || dato.val == 'y') {
                this.setState({ value: "" });
                this.props.onRemoveDato({ key: dato.key, id: dato.id, val: "" });
            }
            else if (lastchr == 'h' || lastchr == 'd' || lastchr == 'w' || lastchr == 'm' || lastchr == 'y') {
                valor = dato.val.replaceAll('h', '').replaceAll('d', '').replaceAll('w', '').replaceAll('m', '').replaceAll('y', '') + lastchr;
                this.setState({ value: valor });
                if (dato.val === "") {
                    this.props.onRemoveDato({ key: dato.key, id: dato.id, val: "" });
                }
                else {
                    this.props.onDato({ key: dato.key, id: dato.id, val: valor });
                }
            }
            else if (lastchr == '0' || lastchr == '1' || lastchr == '2' || lastchr == '3' || lastchr == '4' || lastchr == '5' || lastchr == '5' || lastchr == '6' || lastchr == '7' || lastchr == '8' || lastchr == '9') {
                var unidad = 'd'
                if (dato.val.includes('h')) { unidad = 'h' }
                else if (dato.val.includes('d')) { unidad = 'd' }
                else if (dato.val.includes('w')) { unidad = 'w' }
                else if (dato.val.includes('m')) { unidad = 'm' }
                else if (dato.val.includes('y')) { unidad = 'y' }
                valor = dato.val.replaceAll('h', '').replaceAll('d', '').replaceAll('w', '').replaceAll('m', '').replaceAll('y', '') + unidad;
                this.setState({ value: valor });
                if (dato.val === "") {
                    this.props.onRemoveDato({ key: dato.key, id: dato.id, val: "" });
                }
                else {
                    this.props.onDato({ key: dato.key, id: dato.id, val: valor });
                }
            }
            else {
                this.setState({ value: dato.val });
                if (dato.val === "") {
                    this.props.onRemoveDato({ key: dato.key, id: dato.id, val: this.state.value });
                }
                else {
                    this.props.onDato({ key: dato.key, id: dato.id, val: this.state.value });
                }
            }

        }

    }


    handleValue(a, b) {
        return a === b
    }


    componentWillMount() {
        var nvalue = ""
        this.props.items.map((item) =>
            nvalue = item.value
        )
        this.setState({ value: nvalue });
    }

    render() {

        var idGrupo = this.props.idGrupo
        return (

            <React.Fragment>

                {this.props.items.map((item) => (
                    <React.Fragment>
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <Badge color={this.state.value === "" ? "red" : "green"} empty={true}>&nbsp;</Badge>
                        {item.label}
                        <Input
                            placeholder={"N° seguido del periodo (h,d,w,m,y). Ejemplo: 5d"}
                            key={item.id}
                            fluid={true}
                            rows={1}
                            value={this.state.value}
                            onChange={(value) => this.handleOnChange({ key: idGrupo, id: item.id, val: value })}

                        />
                    </React.Fragment>
                ))}


                {/*<React.Fragment>*/}

                {/*    <LocalizationConfig languageCode={this.state.lng}>*/}
                {/*        <FileDrop*/}
                {/*            label="Fotos del Vehiculo"*/}
                {/*            buttonText="Subir Foto"*/}
                {/*            resetButtonText="Eliminar Fotos"*/}
                {/*            acceptedTypes={['image/*']}*/}
                {/*            placeholder="Arrastrar Foto (maximo: 5MB)"*/}
                {/*            maxSize="1000"*/}
                {/*            error={this.state.fileError}*/}
                {/*            onAccepted={(files) => this.setState({ acceptedFiles: [...acceptedFiles, ...files] })}*/}
                {/*            onRejected={(files) => this.setState({ rejectedFiles: [...rejectedFiles, ...files], fileError: "uno o mas archivos fueron rechazados" })}*/}
                {/*            onReset={() => this.setState({ rejectedFiles: [], acceptedFiles: [], fileError: "" })}*/}
                {/*        />*/}
                {/*        <h5>Fotos Aceptadas:</h5>*/}
                {/*        {acceptedFiles && acceptedFiles.map(file => <p>{file.name} - {file.size} bytes</p>)}*/}
                {/*        <h4>Fotos Rechazadass:</h4>*/}
                {/*        {rejectedFiles && rejectedFiles.map(file => <p>{file.name} - {file.size} bytes</p>)}*/}
                {/*    </LocalizationConfig>*/}
                {/*</React.Fragment>*/}

            </React.Fragment>
        );
    }
}
