import React, { useState, useEffect } from 'react'
import { TimeSeries } from '@scuf/charts'
import { Modal, Badge, InputLabel, Loader, Grid } from '@scuf/common'
//import { useDispatch } from 'react-redux'
import LineData2 from './LineData2'
import LineDataAPI2 from './LineDataAPI2'
import baseServices from '../../../services/base.services';
import './custom.css' 

//require('../../../../node_modules/highcharts/modules/exporting')

//import Highcharts from 'highcharts';
//import HighchartsReact from 'highcharts-react-official';

//import { startLineChart } from '../../../actions/entryDataChart'
// import TitlePage from '../../../components/TitlePage/TitlePage';


const Line = ({ url, body, yAxisTitle, titleCard, randomSeries }) => {

    var date = new Date();
    var last24_utc = Date.UTC(date.getUTCFullYear(), date.getUTCMonth(), date.getUTCDate(),
        date.getUTCHours() - 24, date.getUTCMinutes(), date.getUTCSeconds());

    //const dispatch = useDispatch();
    const [state, setState] = useState([])
    const [modal, setModal] = useState(false);

    const colors = ['rgb(140, 137, 136, 0.3)','rgb(77, 111, 171, 0.3)', 'rgb(218, 130, 79, 0.3)', 'rgb(85, 166, 101, 0.3)', 'rgb(195, 76, 80, 0.3)','rgb(255, 234, 93, 0.3)']
 
    useEffect(() => {


        const getData = async () => {

            try {
                if (body) {
                    var response = await baseServices.getData(body, url);
                    if (response.data && response.data.length > 0) {
                        //dispatch(startLineChart(response.data[0]))                    

                        setState({ line: LineDataAPI2(response), response: response.data, hidden: "hidden" })


                        setTimeout(() => {
                            getData()
                        }, 150000);

                    }
                } else {
                    setState({ line: LineData2(randomSeries) })
                    //dispatch(startLineChart('dummy data'))
                    setTimeout(() => {
                        getData()
                    }, 150000);
                }

            } catch (err) {
                setTimeout(() => {
                    getData()
                }, 15000);

            }


        }

        getData()

    }, [])




    return (
        <>
            {

                <React.Fragment>
                    {
                        modal ?
                            <Modal size="fullscreen" closeIcon={true} onClose={() => setModal(false)} open={modal} closeOnDimmerClick={false} closeOnDocumentClick={false} >
                                <Modal.Header>
                                    {titleCard}
                                </Modal.Header>
                                <Modal.Content>
                                    <TimeSeries
                                        height="480"
                                        showRangeSelector={true}
                                        showScrollbar={false}
                                        showNavigator={true}
                                        zoom="xy"
                                    >
                                        {
                                            state.line && state.line.map((y, i) => (
                                                <TimeSeries.Line spline={true} key={i} name={y.name} data={y.datos} />
                                            ))
                                        }

                                        {/* <TimeSeries.Title onClick={() => setState({ open: true, line: state.line })} className='m-0' text={titleCard} align='left' /> */}
                                        <TimeSeries.Legend enabled={true} position='right middle' itemSpacing='small' layout='Vertical' itemStyle={{ "color": "#E0E0E0", "cursor": "pointer", "fontSize": "10px", "fontWeight": "bold", "textOverflow": "ellipsis" }} />

                                        <TimeSeries.XAxis>
                                            {
                                                state.response && state.response.map((y, i) => (
                                                    y.cluster >= 0 && <TimeSeries.PlotBand key={i} from={y.x} to={y.hasta} color="#939393" />
                                                ))
                                            }
                                        </TimeSeries.XAxis>
                                        <TimeSeries.YAxis tickAmount={6} showGridLines={false} top="0%" height="100%" offset={-10} title={{ text: yAxisTitle }} labels={{ format: '' }} />


                                    </TimeSeries>
                                    {
                                        state.response ?
                                            <React.Fragment><br />Clusterer:<br />
                                                {JSON.stringify(state.response).includes('"cluster":0') && <Badge color="grey">0</Badge>}
                                                {JSON.stringify(state.response).includes('"cluster":1') && <Badge color="grey">1</Badge>}
                                                {JSON.stringify(state.response).includes('"cluster":2') && <Badge color="grey">2</Badge>}
                                                {JSON.stringify(state.response).includes('"cluster":3') && <Badge color="grey">3</Badge>}
                                                {JSON.stringify(state.response).includes('"cluster":4') && <Badge color="grey">4</Badge>}
                                                {JSON.stringify(state.response).includes('"cluster":5') && <Badge color="grey">5</Badge>}
                                            </React.Fragment>
                                            : <React.Fragment />
                                    }
                                </Modal.Content>
                                <Modal.Footer>
                                </Modal.Footer>
                            </Modal>
                            :
                            <div style={{ marginTop: "-1px" }} >
                                <div className={state.hidden}>
                                    <Loader text="Abriendo..." minHeight={400}>
                                    </Loader>
                                </div>
                                <InputLabel label={titleCard} onClick={() => setModal(true)} />
                                <TimeSeries
                                    showRangeSelector={false}
                                    showScrollbar={false}
                                    showNavigator={false}
                                    height={460}
                                >
                                    {
                                        state.line && state.line.map((y, i) => (
                                            <TimeSeries.Line spline={true} key={i} name={y.name} data={y.datos} />
                                        ))
                                    }

                                    {/* <TimeSeries.Title className='m-0' text={titleCard} align='left' /> */}
                                    <TimeSeries.Legend enabled={true} position='right middle' itemSpacing='small' layout='Vertical' itemStyle={{ "color": "#E0E0E0", "cursor": "pointer", "fontSize": "10px", "fontWeight": "bold", "textOverflow": "ellipsis" }} />
                                    <TimeSeries.XAxis>
                                        {
                                            state.response && state.response.map((y, i) => (
                                                y.cluster >= 0 && <TimeSeries.PlotBand key={i} from={y.x} to={y.hasta} color={ colors[y.cluster] } />
                                            ))
                                        }
                                    </TimeSeries.XAxis>
                                    <TimeSeries.YAxis tickAmount={6} showGridLines={false} top="0%" height="100%" offset={-10} title={{ text: yAxisTitle }} labels={{ format: '' }} />

                                </TimeSeries>
                                {

                                    //const colors = ['#8c8988','#4d6fab', '#dc824f', '#55a665', '#c34c50', '#816fae', '#93765d', '#65b1c7']
 
                                    state.response ?
                                    <Grid.Row>
                                        <Grid.Row>
                                        <br />Cluster:<br />
                                        </Grid.Row>
                                        <Grid.Column  width={6}>
                                            {JSON.stringify(state.response).includes('"cluster":0') && <div class='box clust0'>0</div>}
                                        
                                            {JSON.stringify(state.response).includes('"cluster":1') && <div class='box clust1'>1</div>}
                                          
                                            {JSON.stringify(state.response).includes('"cluster":2') && <div class='box clust2'>2</div>}
                                      
                                            {JSON.stringify(state.response).includes('"cluster":3') && <div class='box clust3'>3</div>}
                                            
                                            {JSON.stringify(state.response).includes('"cluster":4') && <div class='box clust4'>4</div>}
                                            {JSON.stringify(state.response).includes('"cluster":5') && <div class='box clust5'>5</div>}
                                            </Grid.Column>
                                        </Grid.Row>
                                        : <React.Fragment />
                                }


                                {/* <div style={{ marginTop: "10px", width: "90%" }} >

                                    <HighchartsReact
                                        highcharts={Highcharts}
                                        //onstructorType={'stockChart'}
                                        options={{
                                            title: {
                                                text: 'My stock chart'
                                            },
                                            series: [{
                                                data: [1, 2, 3]
                                            }]
                                        }}
                                    />
                                </div> */}


                            </div>
                    }

                </React.Fragment>
            }
        </>


    )
}

export default Line
