import axios from "axios";
import authHeader from "./auth-header";

//require('dotenv').config();


const HOST_IP = '10.63.2.160';
//const HOST_IP = window.Configs.PA_DOCKER_HOST;
//const HOST_IP = process.env.REACT_APP_DOCKER_HOST;

//const API_URL = 'http://10.63.2.128:8003/api/';
const API_URL = 'http://' + HOST_IP + ':8003/api/';
//const API_URL = 'http://' + HOST_IP + ':8002/api/';

//console.log(process.env);

const getData = (body, url) => {
  console.log(API_URL + url + body)
  return axios.get(API_URL + url + body, { headers: authHeader(), timeout: 15000 });

};

const delData = (body, url) => {
  console.log(API_URL + url + body)
  return axios.delete(API_URL + url + body, { headers: authHeader(), timeout: 15000 });
};

const postData = (body, url) => {
  console.log(API_URL + url + body)

  return axios.post(API_URL + url, body, { headers: authHeader(), timeout: 15000 });
};



const postFileUpload = (body, url) => {
  //console.log(API_URL + url + body)
  return axios.post(url, body, { headers: authHeader(), timeout: 15000 });
};

const getCSVTags = (body, url) => {
  //console.log(API_URL + url + body)
  return axios.get(url + body, { headers: authHeader(), timeout: 15000 });
};


const exportedObject = {
  getData,
  delData,
  postData,
  postFileUpload,
  getCSVTags
}

export default exportedObject