var layout = {
    plot_bgcolor: '#202020',
    paper_bgcolor: '#202020',
    width: 400,
    height: 400,
    title: {
        text: '',
        font: {
            //family: 'Courier New, monospace',
            size: 20,
            color: '#e0e0e0'
        },
        xref: 'paper',
        x: 0.5,
        y: 0.99
    },
    dragmode: 'zoom',
    // margin: {
    //     r: 10,
    //     t: 25,
    //     b: 40,
    //     l: 60
    // },
    showlegend: false,
    linewidth: 10,
    xaxis: {
        autorange: true,
        showdividers: true,
        dividercolor: '#e0e0e0',
        dividerwidth: 1,
        tickson: "boundaries",
        ticklen: 15,
        tickcolor: "#505050",
        tickfont: {
            size: 12,
            color: '#e0e0e0'
        },
        type: 'multicategory',
        rangeslider: {
            visible: false
        }
    },
    yaxis: {
        autorange: true,
        tickfont: {
            size: 14,
            color: '#e0e0e0'
        },
        type: 'linear'
    }
};


const url = 'modos';
const body = '?id=bplot';
const bodyModels = '?id=models'

const BPlot = () => {

    const [state, setState] = useState({ hidden: "hidden", showplot: false })
    const [models, setModels] = useState({ models: [] });


    useEffect(() => {
        const getModels = async () => {
            try {
                var response = await baseServices.getData(bodyModels, url);
                if (response.data && response.data.length > 0) {
                    setModels({ models: response.data, texto: "-" })
                }

            } catch (err) {
                setTimeout(() => {
                    getModels()
                }, 15000);

            }

        }
        getModels();


    }, []);


    function getDatos(modelo) {
        const getData = async () => {
            setState({ dimensions: '', hidden: "", width: window.innerWidth })
            const bodyplot = body + ";" + modelo

            try {
                if (bodyplot) {
                    var response = await baseServices.getData(bodyplot, url);
                    if (response.data && response.data.length > 0) {
                        setState({ API: response.data, hidden: "hidden", showplot: true })
                        console.log(response.data)

                    }
                } else {
                }

            } catch (err) {
                setTimeout(() => {
                    getData()
                }, 15000);

            }

        }
        return getData();
    }


    /*
    var datos = state.API &&
        [
            {
                type: 'candlestick',
                decreasing: { line: { color: '#7F7F7F' } },
                increasing: { line: { color: '#17BECF' } },
                line: { color: 'rgba(31,119,180,1)' },
                x: [state.API[0].x1, state.API[0].x2],
                low: state.API[0].low,
                open: state.API[0].open,
                close: state.API[0].close,
                high: state.API[0].high
            }
        ];
*/

    var datos = state.API &&
        [
            {
                type: 'candlestick',
                decreasing: { line: { color: '#7F7F7F' } },
                increasing: { line: { color: '#17BECF' } },
                line: { color: 'rgba(31,119,180,1)' },
                x: [state.API[0].x1, state.API[0].x2],
                low: state.API[0].low,
                open: state.API[0].open,
                close: state.API[0].close,
                high: state.API[0].high
            }
        ];



    var datos_features = state.API && new Set(state.API[0].x1);

    var total_clusters = state.API && Array.from(new Set(state.API[0].x2));

    //console.log(datos);






    return (
        <React.Fragment>
            <div className={state.hidden}>
                <Loader text="Abriendo..." minHeight={400}>
                </Loader>
            </div>
            {/* <InputLabel label={JSON.stringify(state.dimensions[0].array_to_json)} /> */}
            <Select placeholder="Seleccione un Modelo" options={models.models} onChange={(value) => getDatos(value)} />

            <Grid.Row style={{paddingTop: '50px'}}>

                {state.showplot && Array.from(datos_features).map((x, i) =>

                    <>
                        <p hidden>{i *= (Math.max(...total_clusters) + 1)}</p>
                        
                        <Grid.Column width={5}>


                            <Plot
                                //data={datosPrueba}
                                data={[
                                    {
                                        type: 'candlestick',
                                        decreasing: { line: { color: '#7F7F7F' } },
                                        increasing: { line: { color: '#17BECF' } },
                                        line: { color: 'rgba(214,214,214)' },
                                        x: [Array(Math.max(...total_clusters) + 1).fill('Clusters'), total_clusters],
                                        low: state.API[0].low.slice(i, i+(Math.max(...total_clusters) + 1)),
                                        open: state.API[0].open.slice(i, i+(Math.max(...total_clusters) + 1)),
                                        close: state.API[0].close.slice(i, i+(Math.max(...total_clusters) + 1)),
                                        high: state.API[0].high.slice(i, i+(Math.max(...total_clusters) + 1)),

                                    }
                                ]}
                                layout={{
                                    plot_bgcolor: '#202020',
                                    paper_bgcolor: '#202020',
                                    width: 500,
                                    height: 400,
                                    title: {
                                        text: x,
                                        font: {
                                            //family: 'Courier New, monospace',
                                            size: 20,
                                            color: '#e0e0e0'
                                        },
                                        xref: 'paper',
                                        x: 0.5,
                                        y: 0.99
                                    },
                                    dragmode: 'zoom',
                                    // margin: {
                                    //     r: 10,
                                    //     t: 25,
                                    //     b: 40,
                                    //     l: 60
                                    // },
                                    showlegend: false,
                                    linewidth: 10,
                                    xaxis: {
                                        autorange: true,
                                        gridcolor: '#464646',
                                        showdividers: true,
                                        dividercolor: '#e0e0e0',
                                        dividerwidth: 1,
                                        tickson: "boundaries",
                                        ticklen: 15,
                                        tickcolor: "#505050",
                                        tickfont: {
                                            size: 12,
                                            color: '#e0e0e0'
                                        },
                                        type: 'multicategory',
                                        rangeslider: {
                                            visible: false
                                        }
                                    },
                                    yaxis: {
                                        autorange: true,
                                        gridcolor: '#464646',
                                        tickfont: {
                                            size: 14,
                                            color: '#e0e0e0'
                                        },
                                        type: 'linear'
                                    }
                                }}
                            />
                        </Grid.Column>

                    </>
                )



                }


            </Grid.Row>

        </React.Fragment>
    );

}


export default BPlot;
