import React from 'react'
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import { Provider } from 'react-redux'
import { store } from './store/store'

import '@scuf/common/honeywell-compact-combined/theme.css'
import '@scuf/charts/honeywell-compact-combined/theme.css'
//import "@scuf/datatable/honeywell-compact-combined/theme.css";

import Layout from './pages/Layout/Layout'
import Login from './pages/Login/Login'
//import Analitica from './pages/Analitica/Analitica';
import DataSources from './pages/DataSources/DataSources';
import Models from './pages/Models/Models'
import Subir from './pages/SubirCSV/csv'
import BoxPlot from './pages/Plots/BoxPlot'
import ParallelPlot from './pages/Plots/ParallelPlot'
import Process from './pages/Process/Process'

const RouteConfig = () => {

  const PrivateRoute = ({ children, ...rest }) => {
    return (
      <Route
        {...rest}
        render={({ location }) =>
          true ? (
            children
          ) : (
            <Redirect
              to={{
                pathname: "/login",
                state: { from: location }
              }}
            />
          )
        }
      />
    );
  }

  return (
    <Provider store={store}>
      <Router>
        <Switch>
          <Route path="/" exact>
            <Layout route='DataSources' component={<DataSources></DataSources>} />
          </Route>
          <Route path="/login">
            <Login />
          </Route>
          <PrivateRoute path="/Models">
            <Layout route='Models' component={<Models></Models>} />
          </PrivateRoute>
          <PrivateRoute path="/DataSources">
            <Layout route='DataSources' component={<DataSources></DataSources>} />
          </PrivateRoute>
          <PrivateRoute path="/Modos de Operacion">
            <Layout route='Modos de Operacion' component={<Process></Process>} />
          </PrivateRoute>
          <PrivateRoute path="/csv">
            <Layout route='Subir csv' component={<Subir></Subir>} />
          </PrivateRoute>
          <PrivateRoute path="/BPlot">
            <Layout route='Box Plot' component={<BoxPlot></BoxPlot>} />
          </PrivateRoute>
          <PrivateRoute path="/PPlot">
            <Layout route='Parallel Plot' component={<ParallelPlot></ParallelPlot>} />
          </PrivateRoute>
        </Switch>
      </Router>
    </Provider>

  )
}

export default RouteConfig
