import React from "react";
import { Table, Button } from '@scuf/common';
import RadioGroup from "./miRadioGroup";
import TextoArea from "./miTextArea"
import InputNumber from "./miInputNumber"
import InputText from "./miInputText"
import InputTextPassword from "./miInputTextPassword"
import InputTextDataRange from "./miInputTextDataRange"
import InputTag from "./miInputTag"
import InputTagMF from "./miInputTagMF"
import Selector from "./miSelector"
import SelectorRSC from "./miSelector_run_serve_criteria"
import Gauge from "./miGauge"
import DTPicker from "./miDatePicker"
//import SubirArchivo from "./miFileUpload"
import SubirArchivo from '../../components/fileupload/miFileUpload'



function Dibuja(props) {

    const dibujar = props.items;
    if (dibujar.type === 'checkboxes') {
        return <React.Fragment>
            <Table.Row>
                <Table.Cell>{dibujar.label}</Table.Cell>
                <Table.Cell textAlign="right"><RadioGroup key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} /> </Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'textareas') {
        return <React.Fragment>
            <Table.Row>
                <Table.Cell> <TextoArea key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} /> </Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'numberareas') {
        return <React.Fragment>
            <Table.Row>
                <Table.Cell> <InputNumber key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} onRemoveDato={props.onRemoveDato} /> </Table.Cell>
                <Table.Cell></Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'inputtext') {
        return <React.Fragment>
            <Table.Row>
                <Table.Cell> <InputText key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} onRemoveDato={props.onRemoveDato} /> </Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'inputtextPassword') {
        return <React.Fragment>
            <Table.Row>
                <Table.Cell> <InputTextPassword key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} onRemoveDato={props.onRemoveDato} /> </Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'inputTag') {
        return <React.Fragment>
            <Table.Row>
                {/* <Table.Cell>{dibujar.label}</Table.Cell> */}
                <Table.Cell> <InputTag key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} onRemoveDato={props.onRemoveDato} /> </Table.Cell>
                <Table.Cell></Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'inputTagMF') {
        return <React.Fragment>
            <Table.Row>
                {/* <Table.Cell>{dibujar.label}</Table.Cell> */}
                <Table.Cell> <InputTagMF key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} onRemoveDato={props.onRemoveDato} /> </Table.Cell>
                <Table.Cell></Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'select') {
        return <React.Fragment>
            <Table.Row>
                <Table.Cell> <Selector key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} onRemoveDato={props.onRemoveDato} /> </Table.Cell>
                <Table.Cell></Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'selectRSC') {
        return <React.Fragment>
            <Table.Row>
                <Table.Cell> <SelectorRSC key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} onRemoveDato={props.onRemoveDato} /> </Table.Cell>
                <Table.Cell></Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'gauges') {
        return <React.Fragment>
            <Table.Row>
                <Table.Cell> <Gauge key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} onRemoveDato={props.onRemoveDato} /> </Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else if (dibujar.type === 'datepickers') {
        return <React.Fragment>
            <Table.Row>
                <Table.Cell>{dibujar.label}</Table.Cell>
                <Table.Cell textAlign="right"> <DTPicker key={props.idGrupo} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} /> </Table.Cell>
            </Table.Row>
        </React.Fragment>
    } else if (dibujar.type === 'fileuploads') {
        return <React.Fragment>
            <Table.Row>
                {/*<Table.Cell>{dibujar.label}</Table.Cell>*/}
                <Table.Cell textAlign="left"> <SubirArchivo key={props.idGrupo} token={props.token} idGrupo={props.idGrupo} items={dibujar.items} onDato={props.onDato} /> </Table.Cell>
            </Table.Row>
        </React.Fragment>
    }
    else return <React.Fragment />


}


export default class General extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeName: "",
            tagItem: { id: 1000, label: 'Tag1', type: 'inputTag', items: [{ id: 1, label: 'Tag', value: '' }] },
            newTags: []


        };
    }



    handleDato = (dato) => {
        this.props.onhandleGeneralDato(dato);
    };

    handleRemoveDato = (dato) => {
        this.props.onhandleGeneralRemoveDato(dato);
    };


    componentWillMount() {
    }

    componentDidMount() {
    }



    render() {
        const Pagina = this.props.id
        return (
            <React.Fragment>

                <Table key={Pagina}>
                    <Table.Header>
                        <Table.HeaderCell content={this.props.titulo} textAlign="left" />
                    </Table.Header>
                    <Table.Body>
                        {

                            this.props.items.map((item) => (
                                <React.Fragment>
                                    <Dibuja key={item.id} token={this.props.token} idGrupo={Pagina + "-" + item.id} items={item} onDato={this.handleDato} onRemoveDato={this.handleRemoveDato} />
                                </React.Fragment>
                            ))
                        }
                        {

                            this.state.newTags.map((item) => (
                                <React.Fragment>
                                    <Dibuja key={item.id} token={0} idGrupo={Pagina + "-" + item.id} items={item} onDato={this.handleDato} onRemoveDato={this.handleRemoveDato} />
                                </React.Fragment>
                            ))
                        }

                    </Table.Body>
                    <Table.Footer>
                        <Table.Row align="left">
                            <Table.HeaderCell >

                            </Table.HeaderCell>
                        </Table.Row>
                    </Table.Footer>
                </Table>

            </React.Fragment>
        );
    }
}