import React, { useState, useEffect } from "react";
import { FileDrop, Loader, Select } from '@scuf/common';
import baseServices from '../../services/base.services';


//const config = require("../config.json");

const url = 'http://10.63.2.160:3000/upload'
// const apiKey = 'config.authorization';
// const headers = { 'authorization': apiKey }

const DTformatOptions = [
    { value: 'dd-mm-yy', text: 'dd-mm-yy' },
    { value: 'dd/mm/yy', text: 'dd/mm/yy' },
    { value: 'dd-mm-yyyy', text: 'dd-mm-yyyy' },
    { value: 'dd/mm/yyyy', text: 'dd/mm/yyyy' },

    { value: 'mm-dd-yy', text: 'mm-dd-yy' },
    { value: 'mm/dd/yy', text: 'mm/dd/yy' },
    { value: 'mm-dd-yyyy', text: 'mm-dd-yyyy' },
    { value: 'mm/dd/yyyy', text: 'mm/dd/yyyy' },

    { value: 'yyyy-mm-dd', text: 'yyyy-mm-dd' }

]



const FileUpload = () => {
    const [state, setState] = useState({ fileError: "", value: "", response: "", acceptedFiles: [], rejectedFiles: [] })
    const [responseAPI, setResponseAPI] = useState([])
    const [formatDT, setFormatDT] = useState('dd-mm-yy')
    const [loader, setLoader] = useState(false)

    const [bodyDS, setBodyDS] = useState([])



    function postFileUpload(body) {
        const FileUpload = async () => {
            try {
                if (body) {
                    var response = await baseServices.postFileUpload(body, url);
                    if (response) {
                        const filename = response.data.data.name
                        /*********************************/
                        setBodyDS([{ idPregunta: "1", Respuesta: filename }, { idPregunta: "2", Respuesta: filename }, { idPregunta: "5", Respuesta: "csv" }, { idPregunta: "6", Respuesta: filename }, { idPregunta: "10", Respuesta: filename }])
                        const bodypost = [{ idPregunta: "1", Respuesta: filename }, { idPregunta: "2", Respuesta: filename }, { idPregunta: "5", Respuesta: "csv" }, { idPregunta: "6", Respuesta: filename }, { idPregunta: "10", Respuesta: filename }, { idPregunta: "11", Respuesta: formatDT }];
                        const url = 'datasources';
                        var responseDS = await baseServices.postData(bodypost, url);
                        setResponseAPI([responseDS])
                        window.location.reload();
                        /*********************************/

                    }
                } else {
                }

            } catch (err) {
                setResponseAPI([err])
            }

        }
        return FileUpload();
    }


    const { acceptedFiles, rejectedFiles } = state;

    return (
        loader ?
            <Loader text="Subiendo Archivo..." minHeight={400}>
                <div className="placeholder" />
            </Loader>
            :
            <React.Fragment>
                <h6>Seleccione el formato de fecha:</h6>
                <div style={{ width: '25%' }}>
                    <Select
                        fluid={true}
                        options={DTformatOptions}
                        value={formatDT}
                        onChange={(value) => setFormatDT(value)}
                    />
                </div>


                <React.Fragment>

                    <FileDrop
                        key={1}
                        label={
                            <React.Fragment>
                                {/* <h5>
                                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                    <Badge color={this.state.value === "" ? "red" : "green"} empty={true} />
                                    {'Seleccine Archivo'}
                                </h5> */}

                            </React.Fragment>
                        }
                        buttonText="Subir archivo CSV"
                        resetButtonText="¿Desea crear el datasource?"
                        acceptedTypes={['.csv']}
                        placeholder="Arrastrar Archivo (máximo: 350MB)"
                        maxSize="350000000"
                        error={state.fileError}
                        onAccepted={(files) => {
                            setState({ acceptedFiles: [...acceptedFiles, ...files] })
                        }
                        }

                        onRejected={(files) => {
                            setState({ rejectedFiles: [...rejectedFiles, ...files], fileError: "Uno o mas archivos fueron rechazados" })
                        }
                        }
                        onReset={() => {
                            setState({ rejectedFiles: [], acceptedFiles: [], fileError: "" })
                            state.acceptedFiles.map((file, index) => {
                                const formData = new FormData();
                                formData.append("file", file);
                                try {
                                    setLoader(true)
                                    postFileUpload(formData)
                                    return null
                                } catch (ex) {
                                    console.log(ex);
                                    return null
                                }
                            }
                            )

                        }

                        }

                    />
                    {/*<h6>Archivos Aceptados:</h6>
                    {acceptedFiles && acceptedFiles.map(file => <p>{file.name} - {file.size} bytes</p>)}
                    <br /><br /><br />
                    <h6>Archivos Rechazados:</h6>
                    {rejectedFiles && rejectedFiles.map(file => <p>{file.name} - {file.size} bytes</p>)} */}
                    {/* {
                    responseAPI.length > 0 ? JSON.stringify(responseAPI[0].data) : null
                }
                <br /><br />
                {
                    bodyDS.length > 0 ? JSON.stringify(bodyDS) : null
                } */}


                </React.Fragment>




            </React.Fragment>
    );


}
export default FileUpload;