import { useState, useEffect } from 'react'
import { Modal, InputLabel, Notification, Input } from '@scuf/common'
import { Icon, Button, Card, Header, Progress, KPIStatistic } from '@scuf/common';
import { DataTable } from '@scuf/datatable';
import { useDispatch } from 'react-redux'
import DataTabDataAPI from './DatTabDataAPI'
import baseServices from '../../../services/base.services';
//import '@scuf/datatable/honeywell-dark/theme.css'
import { Fragment } from 'react';
import Formulario from '../../../components/form/Formulario'
const ReactToastify = require('react-toastify');



const DatTabModel = ({ url, body, title }) => {


    //const dispatch = useDispatch();
    const [state, setState] = useState([])
    const [modal, setModal] = useState(false);
    const [modalEdit, setModalEdit] = useState(false);
    const [selectedRecord, setSelectedRecord] = useState({})
    const [updatedValue, setUpdatedValue] = useState()
    const [add, setAdd] = useState(false);
    const [id, setId] = useState(0);
    const [distModels, setDistModels] = useState([]);
    const [selectedRow, setSelectedRow] = useState([])
    const [isLoading, setIsLoading] = useState(false)
    const [isEntrenando, setIsEntrenando] = useState(false)
    const [avance, setAvance] = useState(0)


    useEffect(() => {


        const getData = async () => {
            try {
                if (body) {
                    var response = await baseServices.getData(body, url);
                    if (response.data && response.data.length > 0) {
                        let datos = DataTabDataAPI(response);

                    
                        setState(datos);

                        const result = [];
                        const map = new Map();
                        for (const item of datos.datos) {
                            if (!map.has(item.model)) {
                                map.set(item.model, true);    // set any value to Map
                                result.push({
                                    model: item.model
                                });
                            }
                        }
                        
                        setDistModels(result);

                    }
                } else { }

            } catch (err) { }

        }
        getData();
        //return { ...state, getData }

    }, [])


    const delData = async () => {
        setIsLoading(true);
        try {
            if (body) {
                let bodydel = '?id=' + selectedRow[0].model;
                var response = await baseServices.delData(bodydel, url);
                window.location.reload();
            } else { }

        } catch (err) { }

    }

    let handleDelete = () => {
        var res;
        //console.log(selectedRow[0].id)
        selectedRow.length < 1
            ?
            res = ReactToastify.toast(<ToastNotification Tipo="Delete" Texto={"Seleccione un Modelo"} closeToast={true} Severity="critical" />)
            :
            res = ReactToastify.toast(<ToastNotification Tipo="Delete" Texto={selectedRow.length == 1 ? "¿Eliminar " + selectedRow[0].model + "?" : "¿Eliminar estos " + selectedRow[0].conn_id + " DataSources?"} closeToast={true} Severity="information" />)

        return res
    }


    let handleAdd = () => {
        var res;
        setAdd(true);
        return res
    }

    let handleEdit = () => {
        var res;
        setAdd(true);
        return res
    }

    let handleEntrenar = () => {
        var res;
        selectedRow.length < 1
            ?
            res = ReactToastify.toast(<ToastNotification Tipo="Script_Init" Texto={"Seleccione un Modelo"} closeToast={true} Severity="critical" />)
            :
            res = ReactToastify.toast(<ToastNotification Tipo="Script_Init" Texto={"¿Entrenar " + selectedRow[0].model + "?"} closeToast={true} Severity="information" Model={selectedRow[0].model} />)

        return res
    }




    const postEntrena = async (model) => {
        try {
            setIsEntrenando(true);
            setIsLoading(true);
            const bodypost = [
                {
                    "Columna": "init_ingesta",
                    "Valor": "1"
                },
                {
                    "Columna": "init_train",
                    "Valor": "1"
                },
                {
                    "Columna": "init_serving",
                    "Valor": "1"
                },
                {
                    "Columna": "progress",
                    "Valor": "0"
                },
                {
                    "Columna": "model",
                    "Valor" : model
                },
            ];
            const url = 'scriptinit';
            //const url = 'scriptinittest';
            var response = await baseServices.postData(bodypost, url);
            getScriptInit();

        } catch (err) { }

    }



    const getScriptInit = async () => {

        try {
            var response = await baseServices.getData('?id=*', 'scriptinit');
            if (response.data && response.data.length > 0) {
                //alert(response.data[0].progress.length);
                //if (response.data[0].init_ingesta == "1" || response.data[0].init_train == "1" || response.data[0].init_serving == "1")
                if (response.data[0].progress.length < 3) {
                    setAvance(response.data[0].progress)
                    setTimeout(() => {
                        getScriptInit()
                    }, 1000);
                } else {
                    setIsEntrenando(false);
                    setIsLoading(false);
                    var res = ReactToastify.toast(<ToastNotification Tipo="Info" Texto={"Modelo Entrenado"} closeToast={true} Severity="information" />)
                }
            }


        } catch (err) {
            setTimeout(() => {
                getScriptInit()
            }, 1000);
        }

    }





    const getData = async () => {
        try {
            if (body) {
                var response = await baseServices.getData(body, url);
                if (response.data && response.data.length > 0) {
                    let datos = DataTabDataAPI(response);

                    setState(datos);

                    const result = [];
                    const map = new Map();
                    for (const item of datos.datos) {
                        if (!map.has(item.model)) {
                            map.set(item.model, true);    // set any value to Map
                            result.push({
                                model: item.model
                            });
                        }
                    }
                    //console.log({ result })
                    //setDistModels(result);

                }
            } else { }

        } catch (err) { }

    }


    const postUpdateModelo = async () => {
        try {
            //setIsEntrenando(true);
            //setIsLoading(true);
            const bodypost = [
                {
                    "columna": "id_conf",
                    "valor": selectedRecord.data.id_conf
                },
                {
                    "columna": "value",
                    "valor": updatedValue
                }
            ];
            const url = 'models';
            var response = await baseServices.postData(bodypost, url);
            getData();
            //setIsLoading(false);

        } catch (err) { }

    }



    const ToastNotification = ({ Tipo, closeToast,Texto, Severity, Model }) => (
        Tipo == "Info"
            ?
            <Notification
                className="toast-notification"
                severity={Severity}
                onCloseClick={closeToast}
                hasIcon={false}
                title={Texto}
                detailsText={<Button key={1} size="small" content="OK" onClick={closeToast} />}
                onDetailsClick={closeToast}
            />
            :
            Tipo == "Delete"
                ?
                Severity == "critical"
                    ?
                    <Notification
                        className="toast-notification"
                        severity={Severity}
                        onCloseClick={closeToast}
                        hasIcon={false}
                        title={Texto}
                        detailsText={<Button key={1} size="small" content="OK" onClick={closeToast} />}
                        onDetailsClick={closeToast}
                    />
                    :
                    <Notification
                        className="toast-notification"
                        severity={Severity}
                        onCloseClick={closeToast}
                        hasIcon={false}
                        title={Texto}
                        detailsText={<Button key={1} size="small" content="Si" onClick={closeToast} />}
                        //detailsText="Eliminar"
                        onDetailsClick={() => { delData() }}
                    />
                :
                Tipo == "Script_Init" ?
                    Severity == "critical"
                        ?
                        <Notification
                            className="toast-notification"
                            severity={Severity}
                            onCloseClick={closeToast}
                            hasIcon={false}
                            title={Texto}
                            detailsText={<Button key={1} size="small" content="OK" onClick={closeToast} />}
                            onDetailsClick={closeToast}
                        />
                        :
                        <Notification
                            className="toast-notification"
                            severity={Severity}
                            onCloseClick={closeToast}
                            hasIcon={false}
                            title={Texto}
                            detailsText={<Button key={1} size="small" content="Si" onClick={closeToast} />}
                            //detailsText="Eliminar"
                            onDetailsClick={() => { postEntrena(Model) }}
                        />
                    :
                    <Fragment />

    )




    const Item = DataTable.HeaderBar.Item;
    return (
        <>
            {
                isLoading ?
                    <Fragment>
                        <Header menu={false} logo={false} title={isEntrenando ? 'Entrenando Modelo... (' + avance + ')' : 'Eliminando Modelo...'}>
                        </Header>
                        <Card>
                            <Card.Header />
                            {/* <Progress percent={50} size="xlarge" /> */}
                            <Card.Content>
                                {/* <Loader minHeight={400}>
                                    <div className="placeholder" />
                                    <Progress percent={avance} circular={true} size='xlarge' />
                                </Loader> */}
                                <Progress percent={avance} circular={true} size='xlarge' />

                            </Card.Content>
                        </Card>
                    </Fragment>
                    :
                    add ?
                        <Formulario />
                        :
                        state.datos ?
                            modal ?

                                <Modal closeIcon={true} onClose={() => { setModal(false); setModalEdit(false) }} open={modal} closeOnDimmerClick={false} closeOnDocumentClick={false}>
                                    <Modal.Header>
                                        {/* {updatedValue} */}
                                        {distModels[id].model}
                                    </Modal.Header>
                                    <Modal.Content>
                                        <Fragment>
                                            <DataTable data={state.datos.filter(x => x.model == distModels[id].model)} rows={15} search={true}
                                                scrollable={true} scrollHeight="600%" reorderableColumns={true} resizableColumns={true}
                                                //onRowClick={(data) => alert(`Fila: ${data.index}, id_conf: ${data.data.id ? JSON.stringify(data.data.id) : data.data.id_conf}`)}
                                                onRowClick={(data) => { setSelectedRecord(data); setUpdatedValue(""); setModalEdit(true) }}
                                            // onCellClick={(data) => alert(JSON.stringify(data))}
                                            >
                                                {
                                                    state.fields && state.fields.map((y, i) => (
                                                        i > 0 && (y.field == 'attribute' || y.field == 'value' || y.field == 'id_conf') ?
                                                            <DataTable.Column key={i} field={y.field} header={y.field} sortable={true} editable={true} editFieldType="text" visible={false} />
                                                            : <Fragment />
                                                    ))
                                                }
                                                <DataTable.Pagination />

                                            </DataTable>

                                        </Fragment>
                                    </Modal.Content>
                                    <Modal.Footer>
                                    </Modal.Footer>

                                    {
                                        modalEdit ?

                                            <Notification
                                                title={selectedRecord.data.attribute}
                                                // tags={['Refinery', 'Pump']}
                                                severity="information"
                                                detailsText="Guardar"
                                                onDetailsClick={() => postUpdateModelo()}
                                                onCloseClick={() => setModalEdit(false)}>
                                                <Input placeholder="Ingrese nuevo valor" onChange={(data) => { setUpdatedValue(data); }} value={updatedValue} />
                                                {/* {<br />}{JSON.stringify(selectedRecord.data)} */}
                                            </Notification>

                                            : <Fragment />
                                    }
                                </Modal>

                                :
                                <Fragment style="margin-bottom: 1000px !important">
                                    <InputLabel label={title} />
                                    <DataTable
                                     data={distModels} rows={10} search={true} selection={selectedRow} onSelectionChange={e => setSelectedRow(e)} selectionMode="single"
                                        scrollable={true} scrollHeight="600%" reorderableColumns={true} resizableColumns={true}
                                        onCellClick={(data) => { setModal(true); setId(data.rowIndex) }}
                                    // onCellClick={(data) => { handleEdit(); setId(data.rowIndex) }}
                                    >
                                        <DataTable.HeaderBar>
                                            <Item
                                                content="Crear"
                                                icon={<Icon name="slidercontrols-plus" size="small" root="common" />}
                                                onClick={() => handleAdd()}
                                            />
                                            <Item
                                                content="Eliminar"
                                                icon={<Icon name="delete" size="small" root="common" />}
                                                onClick={() => handleDelete()}
                                            />
                                            <Item
                                                content="Entrenar"
                                                icon={<Icon name="badge-check" size="small" root="common" />}
                                                onClick={() => handleEntrenar()}
                                            />
                                        </DataTable.HeaderBar>
                                        <DataTable.Column key={1} field="model" header="Modelo" sortable={true} editable={true} editFieldType="text" />

                                        <DataTable.Pagination />

                                    </DataTable>
                                    <br />


                                    {/* <Checkbox label="Entrenar" checked={Entrena} toggle={true} onChange={(checked) => postEntrena()} /><br />
                                    <Checkbox label="Entrenar" toggle={true} /><br />
                                    <Checkbox label="Calcular" toggle={true} /> */}






                                    <ReactToastify.ToastContainer
                                        hideProgressBar={false}
                                        closeOnClick={false}
                                        closeButton={false}
                                        newestOnTop={true}
                                        position="top-center"
                                        autoClose={5000}
                                        toastClassName="toast-notification-wrap"
                                    />

                                </Fragment>

                            : <Fragment>
                                <InputLabel label={title} />
                                <DataTable data={state.datos} >
                                    <DataTable.HeaderBar>
                                        <Item
                                            content="Crear"
                                            icon={<Icon name="slidercontrols-plus" size="small" root="common" />}
                                            onClick={() => handleAdd()}
                                        />
                                        <Item
                                            content="Eliminar"
                                            icon={<Icon name="delete" size="small" root="common" />}
                                        />

                                    </DataTable.HeaderBar>
                                    <DataTable.Column key={1} field="model" header="Modelo" />
                                </DataTable>

                            </Fragment>
            }

        </>


    )
}

export default DatTabModel
