import React, { Fragment } from "react";
import { Input, Badge, Card, Popup } from '@scuf/common';

export default class TextoArea extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fileError: "",
            value: "",
            acceptedFiles: [],
            rejectedFiles: []
        };
    }

    handleOnChange = (dato) => {
        this.setState({ value: dato.val });

        if (dato.val === "") {
            this.props.onRemoveDato(dato);
        }
        else {
            this.props.onDato(dato);
        }
    }


    handleValue(a, b) {
        return a === b
    }


    componentWillMount() {
        var nvalue = ""
        this.props.items.map((item) =>
            nvalue = item.value

        )
        this.setState({ value: nvalue });

        // if (nvalue !== "")
        //     this.handleOnChange({ key: this.props.idGrupo, id: 1, val: nvalue })

    }

    async componentDidMount() {

        if (this.state.value !== "") {
            var dato = await { key: this.props.idGrupo, id: 1, val: this.state.value }
            this.handleOnChange(dato)
        }

    }

    render() {

        var idGrupo = this.props.idGrupo
        return (

            <React.Fragment>
                {this.props.items.map((item) => (
                    <React.Fragment>
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <Badge color={this.state.value === "" ? "red" : "green"} empty={true} >&nbsp;</Badge>


                        <Popup className="popup-theme-wrap" element={item.label} position="right center" on="hover">
                            <Card>
                                <Card.Content>
                                    Grilla de valores que se usarán en la búsqueda del hiperparámetro óptimo para el tamaño mínimo de clusters en %
                                    <br />
                                    <a href="https://hdbscan.readthedocs.io/en/latest/parameter_selection.html#selecting-min-cluster-size" target="_blank">Mas Información</a>
                                </Card.Content>
                            </Card>
                        </Popup>

                        <Input
                            key={item.id}
                            placeholder={"Ej: 10,20,30"}
                            fluid={true}
                            rows={1}
                            value={this.state.value}
                            onChange={(value) => this.handleOnChange({ key: idGrupo, id: item.id, val: value })}

                        />
                    </React.Fragment>
                ))}

            </React.Fragment>
        );
    }
}
